*{
	margin: 0px;
	padding: 0px;
}

html {
	padding: 1em;
	box-sizing: border-box;
}

html, body {
	height: 100%;
}

body {
	overflow-y:scroll;
	display: flex;
	flex-direction: column;
	background: black;
	color: white;
	font-size: 1em;
	line-height: 1.25em;
}

.top {
	display: flex;
	flex-direction: row;
	flex-grow: 1;
}

.bottom {
	display: flex;
	flex-direction: row;

	margin-top: 0.5em;
	margin-bottom: 0.5em;
}

.bottom div:last-child {
	flex-grow: 1;
}

.terminal {
	font-family: 'Unispace', monospace;
	font-weight: normal;
	font-style: normal;
	width: 100%;
}

.terminal .output p {
	margin: 0px;
	display: flex;
	flex: 1;
	flex-wrap: nowrap;
}

.terminal .output p > span {
	display: inline-block;
}

.terminal .output p,
.terminal .output p > span:last-child {
	white-space: pre-wrap;
	max-width: 100%;
}

.terminal input,
.terminal textarea {
	font-family: 'Unispace', monospace;
	pointer-events: none;
	width: 100%;
	height: 1em;
	resize: none;
	overflow: hidden;
	border: none;
	background: black;
	color: white;
	font-size: 1em;
	outline: none;
	padding: 0px;
	box-sizing: border-box;
}